@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,600;0,700;0,800;0,900;1,100;1,200;1,300&display=swap);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.2/css/all.min.css);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Montserrat", sans-serif, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  outline: none;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  cursor: pointer;
  border: none;
}

button:active {
  outline: none;
}

button:active {
  outline: none;
}

p {
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}

ul {
  list-style: none;
}

.App{background-color:#fff;color:#111;min-height:100%;width:100%}
.Header{height:600px;width:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.Header .PerfilImg{width:200px;height:200px;border-radius:50%;box-shadow:rgba(0,0,0,.15) 0px 5px 15px 0px}.Header .Title{font-size:42px;font-weight:600;margin-top:15px}.Header .SubTitle{font-size:20px;font-weight:400;margin:10px}.Header .SocialLinks{display:flex;justify-content:center}.Header .SocialLinks a{margin:0 10px;font-size:30px;color:#111;pointer-events:all;z-index:2}@media(max-width: 800px){.Header{height:400px;padding-top:40px;margin-bottom:-20px}.Header .PerfilImg{max-width:150px;max-height:150px}.Header .Title{font-size:24px}.Header .SubTitle{font-size:16px}.Header .SocialLinks a{font-size:20px}}
.Project{min-height:600px;width:100%;transform:skewY(5deg)}.BgProject{background-color:#f1f1f1}.Container{margin:0;padding:80px 15%;transform:skewY(-5deg)}.InfoContainer{display:flex;justify-content:center;align-items:center}.Reverse{flex-direction:row-reverse}.Project .Container .MainImg{max-width:550px;max-height:350px;margin-right:20px;margin-left:20px}.Project .Container .ProjectTech{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;padding:30px;min-height:100px}.Project .Container .ActionContainer{display:flex;justify-content:center}.Project .Container .ActionBtn{background-color:#549678;padding:10px 50px;color:#fff;font-weight:600;text-decoration:none;z-index:1;box-shadow:rgba(50,50,93,.25) 0px 2px 5px -1px,rgba(0,0,0,.3) 0px 1px 3px -1px}.InfoContainer .Title{font-size:24px;color:#7d7bff;text-decoration:none}.InfoContainer .Title h1{font-weight:500}.InfoContainer .Description{margin-top:10px;margin-bottom:10px}.InfoContainer .TechTitle{font-weight:600;font-size:18px;margin-bottom:10px;color:#555}.InfoContainer .ProjectTech{padding:20px;display:flex;flex-wrap:wrap !important;z-index:1}@media(max-width: 800px){.Container{padding:80px 10%}.InfoContainer .Title{font-size:16px}.Project .Container .MainImg{max-width:100%;height:auto;margin-bottom:20px}.InfoContainer{flex-direction:column;margin:10px 0px}.Reverse{flex-direction:column}}
.Tag{display:flex;justify-content:center;align-items:center;margin:10px 10px;z-index:1}.Tag img{max-width:100px;max-height:35px}
.Navbar{width:100%;text-align:center;text-decoration:none;font-size:20px;padding:15px;position:fixed;top:0;background-color:#93ca44;color:#fff;z-index:1000}.Navbar a{text-decoration:none;font-weight:500;color:#fff}.Navbar a:hover{color:#000;text-decoration:underline}@media(max-width: 800px){.Navbar{font-size:16px;padding:10px;text-align:center}}
.Footer{width:100%;padding:50px;margin-top:20px}.Footer h1{text-align:center;font-weight:300;font-size:38px}.Footer .Tools{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;margin:50px 0}.Footer p{text-align:center}@media(max-width: 800px){.Footer{padding:20px}.Footer h1{font-size:20px}}
